var React = require('react/addons');

var crypto = require('./crypto');

var LoginChallenge = function() {
  this.token = "";
};

LoginChallenge.parse = function(json) {
  var parsed = new LoginChallenge();
  parsed.token = json.Token;
  return parsed;
};

LoginChallenge.prototype.hash = function() {
  var h = new Hasher();
  h.write(nacl.util.decodeUTF8(this.token));
  return h.sum();
};

LoginChallenge.prototype.type = function() {
	return "github.com/jellevandenhooff/chttr.LoginChallenge-0.0"
};

var update = React.addons.update;

var ConfigEmail = React.createClass({
  mixins: [React.addons.LinkedStateMixin],
  getInitialState: function() {
    return {
      email: window.localStorage.getItem("email"),
      device: window.localStorage.getItem("device"),
    };
  },
  next: function(e) {
    e.preventDefault();

    window.localStorage.setItem("email", this.state.email);
    window.localStorage.setItem("device", this.state.device);

    if (window.localStorage.getItem("publicKey") === null) {

      var {publicKey, privateKey} = generateRandomSigningKeypair();
      window.localStorage.setItem("publicKey", publicKey);
      window.localStorage.setItem("privateKey", privateKey);
    } else {
      var publicKey = window.localStorage.getItem("publicKey");
      var privateKey = window.localStorage.getItem("privateKey");
    }

    window.app.setState({phase: VerifyKeyPhase});
  },
  render: function() {
    return (
      <div>
        <p>Welcome to Chttr! To get started, please enter your email address and give this device a name.</p>
        <div className="ui form">
          <div className="field">
            <label>Email address</label>
            <input type="email" placeholder="E-mail address (e.g. alice@gmail.com)"
              valueLink={this.linkState('email')} />
          </div>
          <div className="field">
            <label>Device name</label>
            <input type="text" placeholder="Device name (e.g. laptop)"
              valueLink={this.linkState('device')} />
          </div>
          <div className="ui right labeled icon button" onClick={this.next}>
            <i className="right arrow icon" />
            Configure
          </div>
        </div>
      </div>
    );
  },
});

var downloadJson = function(url) {
  return Promise.resolve($.ajax(url, {dataType: 'json'}));
}

var VerifyKey = React.createClass({
  mixins: [React.addons.LinkedStateMixin],
  getInitialState: function() {
    return {
      email: window.localStorage.getItem("email"),
      device: window.localStorage.getItem("device"),
      publicKey: window.localStorage.getItem("publicKey"),
      checked: false,
    };
  },
  checkKey: async function() {
    try {
      var data = await downloadJson("http://keytree.io/lookup/" + this.state.email);
    } catch (e) {
      this.timer = setTimeout(() => this.checkKey(), 5000);
      return;
    }

    // handle failure, timeout
    this.setState({checked: true});

    var entry = data.Entry;
    if (typeof entry !== 'object' || entry === null) {
      this.timer = setTimeout(() => this.checkKey(), 5000);
      return;
    }

    if (entry.Keys["chttr-" + this.state.device] !== this.state.publicKey) {
      this.timer = setTimeout(() => this.checkKey(), 5000);
      return;
    }

    window.app.setState({phase: ChatPhase});
  },
  componentDidMount: function() {
    this.timer = setTimeout(() => this.checkKey(), 1000);
  },
  componentWillUnmount: function() {
    clearTimeout(this.timer);
  },
  render: function() {
    return (
      <div>
        { this.state.checked ? (
          <div>
            <p>To register this device with KeyTree, please run the following command:</p>
            <pre>
              bin/keytree-client {this.state.email} chttr-{this.state.device}="{this.state.publicKey}"
            </pre>
          </div>) : undefined
        }
        <p>Checking...</p>
      </div>
    );
  },
});

var ConfigEmailPhase = 0, VerifyKeyPhase = 1, ChatPhase = 2;

var Chat = React.createClass({
  getInitialState: function() {
    return {
      connected: false,
      email: localStorage.getItem("email"),
      device: localStorage.getItem("device"),
      publicKey: localStorage.getItem("publicKey"),
      privateKey: localStorage.getItem("privateKey"),
    };
  },
  componentDidMount: async function() {
    this.socket = await Dial("ws://localhost:8080/socket");
    try {
      this.setState({connected: true});

      var challenge = LoginChallenge.parse(await this.socket.read());
      this.socket.send({
        Identity: {Name: this.state.email, Device: this.state.device},
        Signature: sign(this.state.privateKey, challenge),
      });

    } finally {
      this.socket.close();
      this.setState({connected: false});
    }
  },
  componentWillUnmount: function() {
    this.socket.close();
  },
  render: function() {
    return (
      <div>
        <p>Chat: {this.state.email} on {this.state.device}</p>
        <span>State: {this.state.connected ? "connected" : "disconnected"}</span>
      </div>
    );
  }
});

var ChttrApp = React.createClass({
  getInitialState: function() {
    return {
      phase: ConfigEmailPhase,
    };
  },
  render: function() {
    if (this.state.phase === ConfigEmailPhase) {
      return <ConfigEmail />;
    } else if (this.state.phase === VerifyKeyPhase) {
      return <VerifyKey />;
    } else {
      return <Chat />;
    };
  },
});

