var PromiseWebSocket = function(socket) {
  this.socket = socket;

  this.reads = [];
  this.messages = [];

  this.socket.onmessage = (e) => {
    try {
      var json = JSON.parse(e.data);
    } catch (e) {
      this.messages.push({ok: false, exception: e});
      return;
    }
    this.messages.push({ok: true, message: json});
    this.pump();
  };

  this.socket.onclose = (e) => {
    this.socket = undefined;
    this.pump();
  };
};

PromiseWebSocket.prototype.close = function() {
  if (this.socket !== undefined) {
    this.socket.close();
    this.socket.onmessage = undefined;
    this.socket.onclose = undefined;
    this.socket = undefined;
    this.pump();
  }
};

PromiseWebSocket.prototype.read = function() {
  return new Promise((resolve, reject) => {
    this.reads.push({resolve, reject});
    this.pump();
  });
};

PromiseWebSocket.prototype.send = function(message) {
  if (this.socket === undefined) {
    throw "socket closed";
  }
  this.socket.send(JSON.stringify(message));
};

PromiseWebSocket.prototype.pump = function() {
  while (this.messages.length > 0 && this.reads.length > 0) {
    var read = this.reads.pop(0);
    var message = this.messages.pop(0);
    
    if (message.ok) {
      read.resolve(message.message);
    } else {
      read.reject(message.exception);
    }
  }

  if (this.socket === undefined) {
    while (this.reads.length > 0) {
      var read = this.reads.pop(0);
      read.reject("socket closed");
    }
  }
};

var Dial = function(address) {
  return new Promise((resolve, reject) => {
    var socket = new WebSocket(address);
    socket.onopen = () => {
      socket.onopen = undefined;
      socket.onerror = undefined;
      resolve(new PromiseWebSocket(socket));
    };
    socket.onerror = (e) => {
      socket.onopen = undefined;
      socket.onerror = undefined;
      socket.close();
      reject(e);
    };
  });
};

