window.runApp = function() {
  var messages = [];

  var render = function() {
    React.render(<UpdateApp />, document.getElementById('output'));
    // React.render(<Output messages={messages}/>, document.getElementById('output'));
  };

  var pushMessage = function(text) {
    messages.push({id: messages.length, text: text});
    render();
  };

  render();

  var sock = new WebSocket("ws://keytree.io/socket");
  sock.onopen = function() {
    pushMessage("Connected!");
  }
  sock.onmessage = function(event) {
    pushMessage(JSON.stringify(JSON.parse(event.data)));
  };
  sock.onclose = function() {
    pushMessage("Disconnected!");
  }
}

